<template>
  <div v-bind="fullScreenParentProps">
    <a-icon
      v-if="fullScreen"
      class="full-screen-icon"
      :type="iconType"
      @click="() => (fullCoder = !fullCoder)"
    />

    <div class="code-editor-cust full-screen-child">
      <textarea ref="textarea"></textarea>
      <span
        @click="nullTipClick"
        class="null-tip"
        :class="{ 'null-tip-hidden': hasCode }"
        :style="nullTipStyle"
        >{{ placeholderShow }}</span
      >
      <template v-if="languageChange">
        <a-select
          v-model="mode"
          size="small"
          class="code-mode-select"
          @change="changeMode"
          placeholder=""
        >
          <a-select-option
            v-for="mode in modes"
            :key="mode.value"
            :value="mode.value"
          >
            {{ mode.label }}
          </a-select-option>
        </a-select>
      </template>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
// 
import _CodeMirror from "codemirror";

// 
import "codemirror/lib/codemirror.css";
//  options  darcula  gruvbox-dark hopscotch  monokai
// import "codemirror/theme/panda-syntax.css";

import "codemirror/theme/monokai.css"
//css
import "codemirror/addon/hint/show-hint.css";

import "codemirror/addon/hint/show-hint";

import "codemirror/addon/hint/anyword-hint";

// 
// codemirror  /addon/mode/loadmode.js  /mode/meta.js 
//  vue  JS  JS 
import "codemirror/mode/javascript/javascript.js";
// import "codemirror/mode/css/css.js";
// import "codemirror/mode/xml/xml.js";
// import 'codemirror/mode/clike/clike.js'
// import "codemirror/mode/markdown/markdown.js";
import "codemirror/mode/python/python.js";
// import 'codemirror/mode/r/r.js'
// import "codemirror/mode/shell/shell.js";
// import 'codemirror/mode/sql/sql.js'
// import 'codemirror/mode/swift/swift.js'
// import "codemirror/mode/vue/vue.js";

// import { isIE11, isIE } from '@/utils/browser'

// 
const CodeMirror = window.CodeMirror || _CodeMirror;

export default {
  name: "JCodeEditor",
  props: {
    // 
    value: {
      type: String,
      default: "",
    },
    //nocursor
    readOnly:{
      type: [Boolean, String],
      default: false
    },
    // 
    language: {
      type: String,
      default: null,
    },
    languageChange: {
      type: Boolean,
      default: false,
      required: false,
    },
    placeholder: {
      type: String,
      default: null,
    },
    // 
    lineNumbers: {
      type: Boolean,
      default: true,
    },
    // 
    fullScreen: {
      type: Boolean,
      default: false,
    },
    // z-index
    zIndex: {
      type: [Number, String],
      default: 999,
    },
    // StringBoolean
    //  String "!ie" 
    // 
    // IEIEminmax-height
    // "!ie"true
    autoHeight: {
      type: [String, Boolean],
      default: true,
    },
    // 
    height: {
      type: [String, Number],
      default: "240px",
    },
  },
  data() {
    return {
      // 
      code: "",
      iconType: "fullscreen",
      hasCode: false,
      // 
      mode: "python",
      // 
      coder: null,
      // 
      matchBrackets: true,
      options: {
        readOnly: this.readOnly,
        // 
        tabSize: 2,
        //  JS 
        // theme: "panda-syntax",
        theme: "monokai",
        line: true,
        extraKeys: {'Ctrl': 'autocomplete'},//
        hintOptions: {
          tables: {
            users: ["name", "score", "birthDate"],
            countries: ["name", "population", "size"],
          },
        },
      },
      //  JS 
      //  MIME-TYPE  text/ 
      modes: [
        {
          value: "css",
          label: "CSS",
        },
        {
          value: "javascript",
          label: "Javascript",
        },
        {
          value: "html",
          label: "XML/HTML",
        },
        {
          value: "x-java",
          label: "Java",
        },
        {
          value: "x-objectivec",
          label: "Objective-C",
        },
        {
          value: "x-python",
          label: "Python",
        },
        {
          value: "x-rsrc",
          label: "R",
        },
        {
          value: "x-sh",
          label: "Shell",
        },
        {
          value: "x-sql",
          label: "SQL",
        },
        {
          value: "x-swift",
          label: "Swift",
        },
        {
          value: "x-vue",
          label: "Vue",
        },
        {
          value: "markdown",
          label: "Markdown",
        },
      ],
      // code  
      fullCoder: false,
    };
  },
  watch: {
    fullCoder: {
      handler(value) {
        if (value) {
          this.iconType = "fullscreen-exit";
        } else {
          this.iconType = "fullscreen";
        }
      },
    },
    // value: {
    //   immediate: false,
    //   handler(value) {
    //     this._getCoder().then(() => {
    //       this.coder.setValue(value)
    //     })
    //   }
    // },
    language: {
      immediate: true,
      handler(language) {
        this._getCoder().then(() => {
          // 
          if (language) {
            // 
            let modeObj = this._getLanguage(language);

            // 
            if (modeObj) {
              this.mode = modeObj.label;
              this.coder.setOption("mode", `text/${modeObj.value}`);
            }
          }
        });
      },
    },
  },
  computed: {
    placeholderShow() {
      if (this.placeholder == null) {
        return `${this.language}`;
      } else {
        return this.placeholder;
      }
    },
    nullTipStyle() {
      if (this.lineNumbers) {
        return { left: "36px" };
      } else {
        return { left: "12px" };
      }
    },
    // coder 
    coderOptions() {
      return {
        readOnly: this.options.readOnly,
        tabSize: this.options.tabSize,
        theme: this.options.theme,
        lineNumbers: this.lineNumbers,
        line: true,
        extraKeys: this.options.extraKeys,
        hintOptions: this.options.hintOptions,
      };
    },
    isAutoHeight() {
      let { autoHeight } = this;
      // if (
      //   typeof autoHeight === "string" &&
      //   autoHeight.toLowerCase().trim() === "!ie"
      // ) {
      //   autoHeight = !(isIE() || isIE11())
      // } else {
      //   autoHeight = true;
      // }
      return autoHeight;
    },
    fullScreenParentProps() {
      let props = {
        class: {
          "full-screen-parent": true,
          "full-screen": this.fullCoder,
          "auto-height": this.isAutoHeight,
        },
        style: {},
      };
      if (this.fullCoder) {
        props.style["z-index"] = this.zIndex;
      }
      if (!this.isAutoHeight) {
        props.style["height"] =
          typeof this.height === "number" ? this.height + "px" : this.height;
      }
      return props;
    },
  },
  mounted() {
    // 
    this._initialize();
  },
  methods: {
    // 
    _initialize() {
      // 
      this.coder = CodeMirror.fromTextArea(
        this.$refs.textarea,
        this.coderOptions
      );
      // 
      if (this.value || this.code) {
        this.hasCode = true;
        //this.coder.setValue(this.value || this.code)
        this.setCodeContent(this.value || this.code);
      } else {
        this.coder.setValue("");
        this.hasCode = false;
      }
      // 
      this.coder.on("change", (coder) => {
        this.code = coder.getValue();
        if (this.code) {
          this.hasCode = true;
        } else {
          this.hasCode = false;
        }
        if (this.$emit) {
          this.$emit("input", this.code);
        }
      });
      this.coder.on("focus", () => {
        this.hasCode = true;
      });
      this.coder.on("blur", () => {
        if (this.code) {
          this.hasCode = true;
        } else {
          this.hasCode = false;
        }
      });

      //
      // this.coder.on("keyHandled", (e) => {
      //   console.log(e);
      //   this.coder.showHint();
      // });
    },
    getCodeContent() {
      return this.code;
    },
    setCodeContent(val) {
      setTimeout(() => {
        if (!val) {
          this.coder.setValue("");
        } else {
          this.coder.setValue(val);
        }
      }, 300);
    },
    // 
    _getLanguage(language) {
      // 
      return this.modes.find((mode) => {
        // 
        let currentLanguage = language.toLowerCase();
        let currentLabel = mode.label.toLowerCase();
        let currentValue = mode.value.toLowerCase();

        //  java  x-java  value  label 
        return (
          currentLabel === currentLanguage || currentValue === currentLanguage
        );
      });
    },
    _getCoder() {
      let _this = this;
      return new Promise((resolve) => {
        (function get() {
          if (_this.coder) {
            resolve(_this.coder);
          } else {
            setTimeout(get, 10);
          }
        })();
      });
    },
    // 
    changeMode(val) {
      // 
      this.coder.setOption("mode", `text/${val}`);

      // 
      let label = this._getLanguage(val).label.toLowerCase();

      // 
      this.$emit("language-change", label);
    },
    nullTipClick() {
      this.coder.focus();
    },
  },
};
</script>

<style lang="less">
.code-editor-cust {
  flex-grow: 1;
  display: flex;
  position: relative;
  height: 100%;
  .CodeMirror {
    flex-grow: 1;
    z-index: 1;
    .CodeMirror-code {
      line-height: 19px;
    }
  }
  .code-mode-select {
    position: absolute;
    z-index: 2;
    right: 10px;
    top: 10px;
    max-width: 130px;
  }
  .CodeMirror {
    height: auto;
    min-height: 100%;
  }
  .null-tip {
    position: absolute;
    top: 4px;
    left: 36px;
    z-index: 10;
    color: #ffffffc9;
    line-height: initial;
  }
  .null-tip-hidden {
    display: none;
  }
}

/*  */
.full-screen-parent {
  position: relative;

  .full-screen-icon {
    opacity: 0;
    color: black;
    width: 20px;
    height: 20px;
    line-height: 24px;
    background-color: white;
    position: absolute;
    top: 2px;
    right: 2px;
    z-index: 9;
    cursor: pointer;
    transition: opacity 0.3s;
  }

  &:hover {
    .full-screen-icon {
      opacity: 1;

      &:hover {
        background-color: rgba(255, 255, 255, 0.88);
      }
    }
  }

  &.full-screen {
    position: fixed;
    top: 10px;
    left: 10px;
    width: calc(100% - 0px);
    height: calc(100% - 0px);
    padding: 10px;
    background-color: #f5f5f5;

    .full-screen-icon {
      top: 12px;
      right: 12px;
    }

    .full-screen-child {
      height: 100%;
      max-height: 100%;
      min-height: 100%;
    }
  }

  .full-screen-child {
    height: 100%;
  }

  &.auto-height {
    .full-screen-child {
      min-height: 200px;
      max-height: 100%;
      height: unset;
      overflow: hidden;
    }

    &.full-screen .full-screen-child {
      height: 100%;
      max-height: 100%;
      min-height: 100%;
    }
  }
}

.CodeMirror-cursor {
  height: 18.4px !important;
}
</style>


