<template>
  <div>
    <a-layout>
      <a-layout-header style="padding: 0; height: 50px">
        <!--  -->
        <a-menu mode="horizontal" style="color: black; height: 100%">
          <a-sub-menu>
            <span slot="title" class="submenu-title-wrapper">
              <a-icon type="file" />
            </span>
            <a-menu-item-group>
              <a-menu-item key="setting:1">
                <a type="primary" @click="openFile"></a>
              </a-menu-item>
              <a-menu-item key="setting:2">
                <a type="primary" @click="saveFile"></a>
              </a-menu-item>
            </a-menu-item-group>
          </a-sub-menu>

          <a-menu-item>
            <a-input
              v-model="projectName"
              placeholder=""
              style="width: 200px"
            ></a-input>
          </a-menu-item>
          <a-menu-item>
            <a-button type="primary" @click="submitCode"></a-button>
          </a-menu-item>

          <a-menu-item>
            <a-button type="primary" @click="runit"
              ><a-icon type="play-circle" /></a-button
            >
          </a-menu-item>
          <a-menu-item>
            <a-button type="primary" @click="clear"
              ><a-icon type="delete" /></a-button
            >
          </a-menu-item>
        </a-menu>
        <input type="file" id="fileinput" style="display: none" />
      </a-layout-header>
      <a-layout>
        <a-layout-content>
          <a-form-item>
            <JCodeEditor
              language="python"
              v-model="code"
              :fullScreen="true"
              placeholder=""
              ref="codeEditor"
            />
          </a-form-item>
        </a-layout-content>
        <a-layout-sider :collapsible="false" :width="500" :theme="'light'">
          <!--  -->
          <div id="mycanvas"></div>
          <!--  -->
          <pre id="output">{{ out }}</pre>
        </a-layout-sider>
      </a-layout>
      <!-- <a-layout-footer></a-layout-footer> -->
    </a-layout>
  </div>
</template>

<script>
import { saveAs } from "file-saver";
import JCodeEditor from "./JCodeEditor";
import "../python/skulpt.min.js";
import "../python/skulpt-stdlib.js";

import axios from "axios";

const Sk = window.Sk;

export default {
  name: "PythonEditor",
  components: {
    JCodeEditor,
  },
  data() {
    return {
      code: "",
      out: "",
      projectName: "",
    };
  },
  mounted() {},
  created() {
    var that = this
    //
    var url = this.urlParam("url");
    if (url) {
      this.downloadFile(url);
    } else {
      this.downloadFile("./static/defaultPython.py");
    }
    //
     window.document.addEventListener('loadPorject', function(e) {
       console.log("load project:"+e.detail.projectName);
       console.log(e.detail.url);
       that.projectName = e.detail.projectName
       that.downloadFile(e.detail.url)
    });
  },
  methods: {
    runit() {
      var that = this;
      this.clear();
      Sk.pre = "output";
      Sk.configure({ output: that.outf, read: that.builtinRead });
      (Sk.TurtleGraphics || (Sk.TurtleGraphics = {})).target = "mycanvas";
      var myPromise = Sk.misceval.asyncToPromise(function () {
        return Sk.importMainWithBody("<stdin>", false, that.code, true);
      });
      myPromise.then(
        function (mod) {
          that.$message.success("");
        },
        function (err) {
          that.$message.error(err.toString());
        }
      );
    },
    downloadFile(url) {
      var that = this;
      axios.get(url).then((res) => {
        that.setCode(res.data);
      });
    },
    openFile() {
      var that = this;
      var fileInput = document.getElementById("fileinput");
      fileInput.addEventListener("change", function (e) {
        const reader = new FileReader();
        const thisFileInput = e.target;
        reader.onload = function () {
          that.setCode(this.result);
          that.code = this.result;
        };
        reader.readAsText(thisFileInput.files[0]);
      });
      fileInput.click();
    },
    saveFile() {
      var n = new File(
        [this.code],
        (this.projectName ? this.projectName : "") + ".py",
        {
          type: "text/plain;charset=utf-8",
        }
      );
      saveAs(n);
    },
    submitCode() {
      if (this.code == "") {
        that.$message.info("");
      }
      if (window.submitCode) {
        window.submitCode(this.projectName, this.code);
      }
    },
    setCode(code) {
      this.code = code;
      this.$refs.codeEditor.setCodeContent(code);
    },
    clear() {
      document.getElementById("output").innerHTML = "";
      document.getElementById("mycanvas").innerHTML = "";
    },
    outf(v) {
      var mypre = document.getElementById("output");
      mypre.innerHTML = mypre.innerHTML + v;
    },
    builtinRead(n) {
      if (
        Sk.builtinFiles === undefined ||
        Sk.builtinFiles.files[n] === undefined
      )
        throw `File not found: '${n}'`;
      return Sk.builtinFiles.files[n];
    },
    urlParam(name) {
      var results = new RegExp("[\\?&]" + name + "=([^&#]*)").exec(
        window.location.href
      );
      if (!results) {
        return 0;
      }
      return results[1] || 0;
    },
  },
};
</script>

<style lang="less">
.CodeMirror {
  /* Set height, width, borders, and global font properties here */
  font-family: monospace;
  height: calc(100vh - 64px) !important;
  color: black;
  direction: ltr;
}
#output {
  border: 1px solid black;
  height: 40vh;
  width: 100%;
  padding: 10px;
  margin: 10px 0;
}

#mycanvas {
  border: 1px solid black;
  height: 40vh;
  width: 100%;
}
.control {
  margin: 10px;
  button {
    margin: 10px 0;
  }
}
.ant-layout-header {
  background: #dfefff !important;
}
.ant-layout-sider {
  padding: 10px;
}
</style>


